.SingleOperator {
  width: 87%;
  margin-left: 25px;
  margin-top: 15px;
  display: flex;
  flex-direction: column;
}
.SingleOperator .LineOne {
  display: flex;
}
.SingleOperator .LineOne .ReferenceStep {
  width: 50%;
}
.SingleOperator .LineOne .Attribute {
  width: 50%;
  padding-left: 20px;
}
.SingleOperator .LineTwo {
  display: flow-root;
  margin-top: 20px;
}
.SingleOperator .LineTwo .Comparative {
  display: flex;
}
.SingleOperator .LineTwo .Comparative .Compare {
  width: 50%;
}
.SingleOperator .LineTwo .Comparative .CompareNum {
  width: 50%;
  padding-left: 20px;
}
.SingleOperator .LineThree {
  margin-top: 20px;
}
.SingleOperator .LineThree .Actions > ul {
  display: grid;
  background-color: white;
  align-content: space-around;
  align-items: baseline;
  justify-items: stretch;
  overflow: hidden;
}
.SingleOperator .LineThree .Actions > ul > li {
  background-color: white;
}
.SingleOperator .LineThree .Actions > ul > li > div {
  height: 30px;
  padding-left: 0px;
  background-color: #f1f1f1;
}
.SingleOperator .LineThree .Actions > ul > li > ul {
  background-color: white;
  width: 259px;
  margin-left: 50px;
}
.WholeOperatorSequence {
  height: 100%;
}
.WholeOperatorSequence .OperatorSequence {
  height: calc(100% - 79px) !important;
  overflow-y: auto;
  overflow-x: hidden;
  padding-right: 3px;
}
.WholeOperatorSequence .OperatorSequence .OperatorTitle {
  background-color: #F1F8FB;
  text-align: left;
  padding-left: 10px;
  height: 40px;
  padding-top: 10px;
}
.WholeOperatorSequence .OperatorSequence .sequence > ul > li > ul {
  background-color: white;
}
.WholeOperatorSequence .OperatorSequence .sequence > ul > li > div > span {
  color: black;
}
.WholeOperatorSequence .leftTopicPanelButton {
  text-align: center;
}
.WholeOperatorSequence .leftTopicPanelButton .regenerate {
  margin-top: 5px;
}
.WholeOperatorSequence .leftTopicPanelButton .regenerate .regenerate-btn {
  width: 270px;
  height: 8%;
  margin: 0 auto;
  text-align: center;
  background: #F0F0F2;
  border-radius: 3px;
  vertical-align: bottom;
  padding: 5px 0 5px 0;
  border: none;
  color: black;
}
.WholeOperatorSequence .leftTopicPanelButton .regenerate .regenerate-btn:hover {
  background: #FCBE31;
  box-shadow: 0 1px 3px 0 #FCBE31;
}
